.delete-btn {
  float: right;
  color: #e74944;
  border: 1px solid #e74944;
  padding: 6px 30px;
  border-radius: 4px;
  cursor: pointer;
}

.delete-btn:hover {
  color: #000;
  border-color: #000;
}

.save-btn {
  float: right;
  color: rgb(0, 147, 0);
  border: 1px solid rgb(0, 147, 0);
  padding: 5px 20px;
  border-radius: 4px;
  cursor: pointer;
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.list-group-item { padding: 20px 20px; }

.nav-item { margin: 0 15px; }
.nav-item :hover { text-decoration: none; }

nav { 
  background: rgba(0,0,0,0.8);
  position: fixed !important;
  top: 0px;
  width: 100%;
  z-index: 99;
}

nav a {
  color: #FFF;
}
